@use '../base/mixins' as m;
@use '../base/variables' as v;

.contenido_header {
    display: flex;
    flex-direction: column;
    
    @include m.tablet{
        
        h1{

            font-size:3rem;
        }
    }
    @include m.telefono{

        h1{
            
            font-size:1.6rem;
        }
    }
   
}
.derecha{
   display: flex;
  

   .dark-mode{
    margin-left:2rem;
    width:3rem;
    margin-top:2.5rem;
    filter:invert(100%);
    
    &:hover{
        cursor: pointer;
    }
  } 
    
    @include m.telefono{
        display: flex;
        flex-direction: column;
        align-items: center;
      .dark-mode{
        width:3rem;
        margin-left:0;
        margin-top:1rem;
      } 
        
    }
    @include m.tablet{
        display: flex;
        flex-direction:column;
        align-items: center;
      
    }
}


.barra {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    
    img {
        
        width:350px;
    }
    @include m.tablet{
        display: flex;
        flex-direction:column;
       

    }
    @include m.telefono{
        display: flex;
        flex-direction: column;
        align-items:center;
    
    }
  
}


.navegacion {
    padding-top: 25px;
    

    a {
        color: v.$blanco;
        margin: .3125rem;
        text-decoration: none;
        font-size: 2.2rem;
        margin-left: 15px;
        position: relative;
        display: inline-block;

      
        &::after {
            content: '';
            position: absolute;
            width: 0%; 
            height: 2px; 
            background-color: v.$verde; 
            bottom: 0; 
            left: 0; 
            transition: width 0.3s ease-in-out; 
        }

        &:hover {
            color: v.$verde; 

            &::after {
                width: 100%; 
            }
        }
    }

    @include m.tablet {
        display: flex;
        flex-direction:row;
       

        a {
            font-size: 1.8rem;
            margin-top: .3125rem;
        }
    }
    @include m.telefono {
        display: flex;
        flex-direction: column;
        display: none;
       

        a {
            font-size: 1.8rem;
            margin-top: .3125rem;
        }
    }
}

.mobile-menu{

    @include m.desktop{
        display: none;
    }
    @include m.tablet{
        display: none;
    }

    @include m.telefono{
        

        img{
            width: 5rem;
        }
    }
}

.mostrar{
   
    @include m.telefono{
    
    display: block;
    
    a{
        display: flex;
        flex-direction: column;
    }
    }
    
}
